import "./App.css";

import Footer from "./components/footer/Footer";
import Header from "./components/header/Header.jsx";
import Homepage from "./screens/Home/Homepage";
import Product from "./screens/Product/Product";
import Search from "./screens/Search/Search";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Brand from "./screens/Brand/Brands";
import CategoryResults from "./components/CategoryGrid/CategoryResults";
function App(props) {
  return (
    <Router>
      <Header />
      <Switch>
        <Route path="/Search/:search" component={Search} />
        <Route path="/Product/:id" component={Product} />
        <Route path="/Brand/:id" component={Brand} />
        <Route exact path="/prpsons/" component={Homepage} />
        <Route path="/category/:id" component={CategoryResults} />
      </Switch>

      <Footer />
    </Router>
  );
}

export default App;
