import { useEffect, useState } from "react";
import api from "../../api/main";
import BigCard from "../../components/Cards/BigCard";
export default function CategoryResults(props) {
  const [products, setproducts] = useState([]);
  useEffect(() => {
    api
      .get("/category_products/" + props.match.params.id + "/")
      .then((response) => setproducts(response.data));
    return () => {};
  }, []);
  console.log("category > ", products);
  return (
    <div>
      <div className="bigHeading">
        <b style={{ marginTop: "10px" }}>
          <span style={{ color: "grey" }}>Showing Category</span> &nbsp;
          {products.length !== 0 && products[0].category}
        </b>
      </div>
      <div className="searchContainer">
        {products.map((items) => (
          <BigCard
            link={"/Product/" + items.id}
            title={items.title}
            brand={items.brand}
            price={items.discounted_price}
            image={"http://localhost:8000" + items.image1}
          />
        ))}
      </div>
    </div>
  );
}
