import Search from "../Search/Search";
import React, { useEffect, useState } from "react";
import api from "../../api/main";
import BigCardNScroll from "../../components/Mixed/BigCardNScroll";

export default function Brand(props) {
  const [Product, setProduct] = useState([]);
  const [brand, setBrand] = useState([]);

  useEffect(() => {
    api
      .get("/brand/" + props.match.params.id + "/")
      .then((response) => setBrand(response.data[0]));
    return () => {};
  }, [props.match.params.id]);

  useEffect(() => {
    api
      .get("/brand_products/" + props.match.params.id + "/")
      .then((response) => setProduct(response.data));
    return () => {};
  }, [props.match.params.id]);
  return (
    <div>
      <Search brand={brand} products={Product} />
    </div>
  );
}
