import "./ProductCard.css";
import { JsonToTable } from "react-json-to-table";
import { useState, useEffect } from "react";

export default function ProductCard({ productData }) {
  const [PreviewImage, setPreviewImage] = useState("");

  useEffect(() => {
    if (productData.length !== 0) {
      setPreviewImage(productData.image1);
    }
    return () => {};
  }, [productData.image1]);

  const handleThumb = (image) => {
    setPreviewImage(image);
  };

  return (
    <div className="productContainer">
      {productData.length !== 0 && (
        <>
          <div className="ProductImage">
            <div className="thumb">
              <img
                src={"http://localhost:8000" + productData.image1}
                onClick={() => {
                  setPreviewImage(productData.image1);
                }}
              />
              <img
                src={"http://localhost:8000" + productData.image2}
                onClick={() => {
                  setPreviewImage(productData.image2);
                }}
              />
              <img
                src={"http://localhost:8000" + productData.image3}
                onClick={() => {
                  setPreviewImage(productData.image3);
                }}
              />
              <img
                src={"http://localhost:8000" + productData.image4}
                onClick={() => {
                  setPreviewImage(productData.image4);
                }}
              />
              <img
                src={"http://localhost:8000" + productData.image5}
                onClick={() => {
                  setPreviewImage(productData.image5);
                }}
              />
            </div>
            <div className="preview">
              <img src={"http://localhost:8000" + PreviewImage} />
            </div>
          </div>
          <div className="ProductDetail">
            <span style={{ fontSize: "24px" }}>{productData.title}</span>
            <p style={{ fontSize: "14px" }}>{productData.description}</p>
            <div className="buttonSection">
              <div className="buyButton">
                From ₹ {productData.discounted_price}
              </div>
              <div className="actualPrice">
                Actual Price ₹ {productData.original_price}{" "}
              </div>
            </div>
            <div className="specs">
              <table>
                {Object.keys(productData.details).map((item) => (
                  <tr>
                    <td>{item}</td>
                    <td>{productData.details[item]}</td>
                  </tr>
                ))}
              </table>
            </div>
          </div>
        </>
      )}
    </div>
  );
}
