import ButterScroll from "../ButterScroll/ButterScroll";
import BigCard from "../Cards/BigCard";

export default function BigCardNScroll(props) {
  return (
    <>
      <div className="mediumHeading">
        <p>
          <b>
            <span style={{ color: "grey" }}>{props.heading}</span>
          </b>
        </p>
      </div>

      {/* slider small */}
      <ButterScroll>
        {props.cardData.map((item) => (
          <BigCard
            link={"/Product/" + item.id}
            title={item.title}
            brand={item.brand}
            price={item.discounted_price}
            image={"http://localhost:8000" + item.image1}
          />
        ))}
      </ButterScroll>
    </>
  );
}
