import "./Footer.css";
import {
  FacebookFilled,
  InstagramFilled,
  MailFilled,
  WhatsAppOutlined,
  CopyrightOutlined,
} from "@ant-design/icons";
import { useEffect, useState } from "react";
import api from "../../api/main";

export default function Footer() {
  const IconStyle = {
    fontSize: "26px",
    marginLeft: "30px",
    marginRight: "30px",
  };
  const [FooterDetails, setFooterDetails] = useState([]);

  useEffect(() => {
    api.get("/siteMeta/").then((response) => {
      if (response.data[0] !== undefined) {
        setFooterDetails(response.data[0]);
      }
    });
    return () => {};
  }, []);
  return (
    <div className="FooterContainer">
      {FooterDetails.length !== 0 && (
        <>
          <div className="FooterMain">
            <div className="FooterList">
              <span className="FooterTitle">About</span>
              <span className="FooterLinks">
                {Object.keys(FooterDetails.about).map((item) => (
                  <a href={FooterDetails.about[item]}>{item}</a>
                ))}
              </span>
            </div>

            <div className="FooterList">
              <span className="FooterTitle">Contact</span>
              <span className="FooterLinks">
                {Object.keys(FooterDetails.contact_us).map((item) => (
                  <a href="/">
                    {item}: {FooterDetails.contact_us[item]}
                  </a>
                ))}
              </span>
            </div>

            <div className="FooterList">
              <span className="FooterTitle">Shop</span>
              <span className="FooterLinks">
                {Object.keys(FooterDetails.address).map((item) => (
                  <a href="/">{FooterDetails.address[item]}</a>
                ))}
              </span>
            </div>
          </div>
          <div className="FooterSocial">
            <FacebookFilled style={{ ...IconStyle, color: "#08c" }} />
            <InstagramFilled style={{ ...IconStyle, color: "#E1306C" }} />
            <MailFilled style={{ ...IconStyle, color: "#08c" }} />
            <WhatsAppOutlined style={{ ...IconStyle, color: "green" }} />
          </div>
          <div className="FooterFooter">
            <div className="FooterLeftLink">
              {Object.keys(FooterDetails.footer_meta).map((item) => (
                <a href={FooterDetails.footer_meta[item]}>{item}</a>
              ))}
            </div>
            <div className="FooterRightLink">
              <a href="">
                <CopyrightOutlined
                  style={{
                    fontSize: "20px",
                    color: "white",
                    marginRight: "5px",
                    position: "relative",
                    top: "2px",
                  }}
                />
              </a>
              <a href="">2021 PrpSons.com</a>
            </div>
          </div>
        </>
      )}
    </div>
  );
}
