import api from "../../api/main";
import { useEffect, useState } from "react";
import BigCardNScroll from "./BigCardNScroll";
export default function MostValuableProducts() {
  const [Product, setProduct] = useState([]);
  useEffect(() => {
    api.get("/product/").then((response) => setProduct(response.data));
    return () => {};
  }, []);
  return (
    <div>
      <BigCardNScroll heading="Most Valuable Products" cardData={Product} />
    </div>
  );
}
