import { useEffect, useState } from "react";
import api from "../../api/main";
import BigCardNScroll from "../../components/Mixed/BigCardNScroll";
import ProductCard from "../../components/ProductCard/ProductCard";

export default function Product(props) {
  const [Products, setProducts] = useState([]);
  const [Recommended, setRecommended] = useState([]);

  useEffect(() => {
    api
      .get("/product/" + props.match.params.id + "/")
      .then((response) => setProducts(response.data[0]));
    return () => {};
  }, [props.match.params.id]);

  useEffect(() => {
    api.get("/product/").then((response) => setRecommended(response.data));
    return () => {};
  }, []);
  return (
    <div>
      <ProductCard productData={Products} />
      <BigCardNScroll
        heading="Other Recommended Products"
        cardData={Recommended}
      />
    </div>
  );
}
